header{
	height: 71px;
}
.container{

	width: 90%;
	margin: auto;
	height: 100%;
}
nav{
	position: relative;
}
.activeLink{
	position: absolute;
	height: 2.5px;
	background: #000;
	bottom: -4px;
	transition: all 0.5s;
}

.menu{
    background:#E5E5E5;
	font-family: "Roboto";
	font-size: 14px;
    display: flex;
    justify-content: center;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 70px;
    align-items: center
}

.logo{
	position:  absolute;
	left:  0;
	top: 0;
    height: 100%;
}

.list{

	display: flex;
	justify-content: center;
	align-content: center;

}
.link{
	width: 63px;
    border: none;
    background: none;
	color: #000;
}

.element{

	padding-right: 64px; 

}

