.container{

	width: 90%;
	margin: auto;
	height: 100%;
}
.grid{
	grid-gap: 10px;
	display: grid;
}
.name{
	font-size: 50px;
	font-family:  "Roboto";
	padding: 20px;
}
.main{
	position: relative;
	border-top: 2px solid black;
	padding:  20px;
	font-family: "Roboto";
}
.main::before{
	transition: all 1s;
	position: absolute;
	content: "";
	width: 20px;
	height: 2px;
	background: black;
	right:  20px;
	top: 50%;
	margin-top: -2.5px;
}
.main::after{
	transition: all 1s;
	position: absolute;
	content: "";
	width: 20px;
	height: 2px;
	background: black;
	right:  20px;
	transform: rotate(90deg);
	top: 50%;
	margin-top: -2.5px;
}

.burger{
	font-family: "Roboto";
	line-height: 20px;
	width: 80%;
}
.text{
	padding: 20px;
	word-break: break-word;
	font-family: "Raleway";
}
.burgerName{
	font-size: 2em ;
	text-transform: uppercase;
}
.hidden{
	transition: all 1s;
	overflow: hidden;
	display: block;
	height: 0;		
}
.active .hidden{
	transition: all 1s;
	height:  200px;
}
.active .main::after{
	transition: all 1s;
	transform: rotateZ(405deg);
}
.active .main::before{
	transition: all 1s;
	transform: rotateZ(315deg);
}
.inf{
    word-break: break-word;
	font-family: "Raleway";
}