@font-face {

	font-family: 'Roboto';
	src: url('./../../assets/fonts/roboto/Roboto-Medium.eot');
	src: local('☺'), url('./../../assets/fonts/roboto/Roboto-Medium.woff') format('woff'), url('./../../assets/fonts/roboto/Roboto-Medium.ttf') format('truetype'), url('../../assets/fonts/roboto/Roboto-Medium.svg') format('svg');
	font-weight: normal;
	font-style: normal;

}

@font-face {

	font-family: 'Raleway';
	src: url('./../../assets/fonts/raleway/Raleway-Light.eot');
	src: local('☺'), url('./../../assets/fonts/raleway/Raleway-Light.woff') format('woff'), url('./../../assets/fonts/raleway/Raleway-Light.ttf') format('truetype'), url('../../assets/fonts/raleway/Raleway-Light.svg') format('svg');
	font-weight: normal;
	font-style: normal;

}

 .container{

	width: 90%;
	margin: auto;
	height: 100%;
}

html{
	scroll-behavior: smooth;
}
main{
	width: 100%;
    height: 90vh;
    background-size: cover;

}

.banner{
	color: white;
	display: grid;
	grid-template-columns:  1fr;
	grid-template-rows: 1fr 1fr 1fr;
	height: 100%;

}

.mainHeadlineContainer{
    color: white;
    justify-self: center;
	grid-row:  2/2;

}

.start{
    background-image: url(../../assets/img/bg.jpg);
	height: 90vh;
	background-size: cover;

}

.date{
	color: #fff;
	font-family: "Raleway";
	font-size: 2em;

}

.mainHeadline{
	color: #fff;
	font-family: "Roboto";
	font-size: 2.5em;

}
.link{

    border-radius:5px;
	display:  flex;
	justify-content: center;
	align-items: center;
	font-family: "Roboto";
	margin-top: 20px;
	width: 30%;
	height: 60px;
	background:  #fff;
	border: none;
	font-size: 1.5em;
	color: #000;
	letter-spacing: 0.05em;
	cursor: pointer;

}

.about{
	font-size: 36px;
	font-family: "Roboto";
}
.firstHeadline{
	margin: 65px 0;
}

.inf{
	padding-bottom: 10px;
	line-height: 30px;
	margin-left: 20px;
}

.inf p {
	font-size: 25px;
	font-family: "Raleway";
}

.list{
	font-size: 25px;
	font-family: "Raleway";	
}

.news{

	padding-bottom: 40px;

}

.header{

	text-transform: uppercase;
	margin-top: 100px;
	font-family: "Roboto";
	font-size: 30px;

}


.newsContainer{
	margin-top: 30px;
	display: grid;
	grid-template-columns: 1fr 1fr 1fr;
	grid-gap: 10px;

}

.block{

	font-family: "Roboto";

}

.img1{

    
	height: 200px;
	background-size: cover;
    background-position-x: center;
	width: 100%;

}
.img2{

   
	height: 200px;
	background-size: cover;
    background-position-x: center;
	width: 100%;

}

.img3{

    
	height: 200px;
	background-size: cover;
    background-position-x: center;
    background-position-y: center;
	width: 100%;

}

.img4{

    
	height: 200px;
	background-size: cover;
    background-position-x: center;
    background-position-y: center;
	width: 100%;

}



.name{

	margin-top: 10px;

}

.description{

	margin-top: 10px;
	color: #9C9999;

}
.allNews{
	display: block;	
    margin: auto;
    background: none;
    border: 2px solid #1C192D;
    padding: 20px 50px;
    text-transform: uppercase;
    font-family: "Roboto";
    width:  150px;
    text-align: center;
}
