.container{

	width: 90%;
	margin: auto;
	height: 100%;
}

.news{

	padding-bottom: 40px;

}

.header{

	text-transform: uppercase;
	margin-top: 100px;
	font-family: "Roboto";
	font-size: 30px;

}


.newsContainer{

	margin-top: 30px;
	display: grid;
	grid-template-columns: 1fr 1fr 1fr;
	grid-gap: 10px;

}