@font-face {
	font-family: 'karlocham';
	src: url('../../assets/fonts/karlocham/KarloCham-Line.eot');
	src: local('☺'), url('../../assets/fonts/karlocham/KarloCham-Line.woff') format('woff'), url('../../assets/fonts/karlocham/KarloCham-Line.ttf') format('truetype'), url('../../assets/fonts/karlocham/KarloCham-Line.svg') format('svg');
	font-weight: normal;
	font-style: normal;
}

.try{
    color: aqua;
}

.container{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 90vh;
    margin: 0 auto;
}
.mistake{
    color: #954A4A;
    font-size: 20em;
    font-family: 'karlocham';
}
.description{
    font-style: uppercase;
    color: #2D5959;
    font-size: 4.5em;
    font-family:'karlocham';
}
.link{

    margin: 0;
    overflow: hidden;
    position: relative;
    margin-top: 20px;
    color: #2D5959;
    border: #2D5959  solid 10px;
    padding: 10px;
    font-size: 4em;
    font-family:'karlocham';
    transition: all 1s;
}

.link:hover{
    color: whitesmoke;
    border: solid #954A4A 10px;
    background-color: #954A4A;
}

