.container{
    position: absolute;
    width: 70%;
    margin: auto auto;
    display: grid;
    justify-content: center;
    grid-gap: 20px;
    left: 15%;
    top: 10vh;
    padding: 100px 0;
    background-color: cadetblue;

}
.header{
    font-size: 5em;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}
.form{
    justify-self: center;
    display: grid;
    grid-gap: 10px;
}
.login{
    border: 2px solid black;
    padding: 10px;
}
.password{
    border: 2px solid black;
    padding: 10px;

}
.submitButton{
    border: none;
    background-color: rgb(234, 163, 56);
    padding: 10px;
}